@import "../../styles/mixins";

.modal {
  height: 100vh;
  width: 100vw;
  background-color: rgba(0, 0, 0, 0.6);
  position: fixed;
  z-index: 2;
  top: 0;
  left: 0;
  @include flexCentered;
  opacity: 0;
  pointer-events: none;
  transition: 0.5s;

  &--active {
    opacity: 1;
    pointer-events: all;
  }

  &__content {
    width: 350px;
    border-radius: 3rem;
    padding: 4rem;
    @include flexAlignCenterColumn;
    background-color: var(--bg);
    transform: scale(0.5);
    transition: 0.5s all;

    &--active {
      transform: scale(1);
    }
  }
}







